.all-btn {
  position: absolute;
  top: 8px;
  left: 8px;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  font-size: 24px;
  color: #949494;
  background-color: rgba(0, 0, 0, 0.003);
  cursor: pointer;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);

  &.completed {
    color: #484848;
  }
}

.main-input {
  width: 100%;
  height: 65px;
  padding: 16px 16px 16px 60px;
  font-size: 24px;
  background-color: rgba(0, 0, 0, 0.003);
  box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
  outline: none;

  &:focus {
    box-shadow: 0 0 2px 2px #cf7d7d;
  }
}

@media (max-width: 480px) {
  .all-btn {
    top: 6px;
    left: 13px;
    width: 38px;
    height: 38px;
  }
  .main-input {
    height: 50px;
    font-size: 18px;
  }
}
